import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { tokenNotExpired } from 'angular2-jwt';
import 'rxjs/add/operator/map';


@Injectable()
export class ApiService {

  authToken: any;
  user: any;
  vehiculos: any;

  constructor(private http: Http) { }

  setEmployeeData(data) {
    // Pedir el ID del usuario al cual corresponden los datos a actualizar
    data.id = JSON.parse(localStorage.getItem('user')).id;
    console.log('Datos: ', data);

    // Settear los encabezados para la petición al API
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    // Hacer la petición, se retorna una promesa
    return this.http.post('users/datos-empleado', data, { headers })
      .map(res => res.json());
  }

  setVehicleData(data) {
    // Pedir el ID del usuario al cual corresponden los datos a actualizar y agregarlo a los datos
    data.idCliente = JSON.parse(localStorage.getItem('user')).id;

    // Settear los encabezados para la petición al API
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    // Hacer la petición, se retorna una promesa
    return this.http.post('users/registrar-vehiculo', data, { headers })
      .map(res => res.json());
  }

  getVehicles() {
    let headers = new Headers();

    // Busca el token del usuario que esta ingresado en el sistema actualmente
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');

    return this.http.get('users/vehiculos', { headers })
      .map(res => res.json());
  }

  eliminarVehiculo(id) {
    // Settear los encabezados para la petición al API
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    // Hacer la petición, se retorna una promesa
    return this.http.post('users/eliminar-vehiculo', {id}, { headers: headers })
      .map(res => res.json());
  }

  crearOrden(data) {
    // Settear los encabezados para la petición al API
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');

    // Hacer la petición, se retorna una promesa
    return this.http.post('users/solicitar-orden', data, { headers: headers })
      .map(res => res.json());
  }

  loadToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }
}



